header {
  color: $text-fair-color;
  text-align: center;
  background: url(../images/background/header-background-w1100.png); //temporary
}

.header {
  margin: 0 5%;

  &__title {
    margin: 3rem auto 1rem;
    font-family: "Seaside Resort NF";
    color: $text-fair-color;
    text-shadow: 3px 0px rgba(75, 78, 76, 0.7);
  }

  &__ribbon {
    display: inline-block;
    margin: 1rem 0;
    overflow: visible;

    &--image {
      width: 100%;
    }
  }

  &__slider {
    display: block;
    overflow: hidden;
    margin: 1rem 0;
    padding: 0.5rem;
    background-color: rgba(251, 250, 246, 1);
    @include element-shadow;
  }

  &__slides {
    position: relative;
    background-color: black; // temporary
    width: 100%;
    height: 100%;
    max-height: 24.5rem;
    overflow: hidden;
    @include flex-row;
    justify-content: center;
    flex-wrap: wrap;

    .slider__navigation {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      background-color: transparent;
      @include flex-row;
      justify-content: space-between;
      z-index: 1;

      .slider__button {
        font-size: 2rem;
        height: 5rem;
        width: 1.8rem;
        background-color: $blue-color;
        opacity: 0.6;
        border: none;
        cursor: pointer;
        @include transition-ease;

        &--previous {
          border-radius: 0 50% 50% 0;
        }

        &--next {
          border-radius: 50% 0 0 50%;
        }

        &:focus,
        &:hover {
          scale: 1.1;
          opacity: 0.9;
          color: $text-fair-color;
          box-shadow: 0 0 1rem $text-fair-color;
        }
      }
    }

    .slider__dots {
      position: absolute;
      bottom: 0.5rem;
      right: 0;
      left: 0;
      list-style: none;
      @include flex-row;
      justify-content: center;
      opacity: 0.9;
      z-index: 1;

      .slider__dot {
        margin: 0 0.5rem;
        border-radius: 50%;
        line-height: 0.1;
        transition: all 1s ease;

        .slider__dots--button {
          cursor: pointer;
          background-color: $blue-color;
          width: 1rem;
          height: 1rem;
          border-radius: 50%;
          border: none;
          transition: all 1s ease;
        }

        &--active {
          border: 2px solid $blue-color;

          .slider__dots--button {
            background-color: $text-fair-color;
          }
        }
      }
    }
  }

  &__slide {
    height: 0px;
    overflow: hidden;
    transition: transform 1s ease;
    transform: translateX(-100%);

    &--active {
      height: 100%;
      transform: translateX(0);
    }

    &--next {
      transform: translateX(100%);
    }
  }

  .slider__caption { 
    color: rgba(0, 0, 0, 0.6);
    line-height: 1.5;
  }

  &__caption {
    font-size: 3.2rem;
    color: $text-fair-color;
    line-height: 1.3;
    margin-top: 2.5rem;

    &--second-line {
      font-size: 2rem;
      line-height: 1.4;
      margin-bottom: 3rem;
    }
  }

  &__dashline {
    @include dashline-first;
    border-color: $darkgrey-color;

    &--bottom {
      @include dashline-second;
      border-color: $darkgrey-color;
    }
  }
}